.pl {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pl-texts{
    width: 65%
}
.pl-title{
    font-style: 50px;
    font-weight: 600;
}
.pl-desc{
    margin: 20px 0x;
}
.pl-list{
    /* background-color: red; */
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media screen and  (max-width: 480px){
    .pl{
        padding: 20px;
    }
    .pl-texts{
        width: 100%;
    }
}