*{
    font-family: 'Poppins', sans-serif;
}
.i{
    display: flex;
    height: 100vh;
    /* background-color: aquamarine; */
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex: 1;
    position: relative;
    
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-intro{
    font-size: 30px;
    font-weight: 300;
}
.i-name{
    font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-item{
    height: 50px;
    font-size:30px;
    font-weight: bold;
    color:darkgreen;
    display: flex;
    align-items: center;

}
.i-bg{
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: darkgreen;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;

}
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}
.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate
}
.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

@keyframes move {
    25%{
        transform: translateY(-50px)
    }
    50%{
        transform: translateY(-100px)
    }
    75%{
        transform: translateY(-150px)
    }
    100%{
        transform: translateY(-200px)
    }
}

@media screen and  (max-width: 480px){
    .i{
        flex-direction: column;
    }
    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
}