.c{
    height: 100vh;
    position: relative;
}

.c-bg{
    width: 20px;
    height: 100%;
    position: absolute;

}
.c-wrapper{
    padding: 50px;
    display: flex;
}
.c-left{
    flex: 1;
}
.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title{
    font-size: 60px;
    width: 80%;
}
.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%

    
}
.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 29px;
}
.c-icon-1{
    width: 60px;
    margin-left: -12px;
    margin-right: 20px;
}
.btn{
    
}
.c-desc{
    font-weight: 300;
}
form{
    margin-top: 20px;

}
input{
    height: 50px;
    width: 50%;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button{
    border: none;
    padding: 15px;
    background-color: rgb(29, 121, 29);
    font-weight: 500;
    cursor: pointer;
    color: aliceblue;
  

}
button:hover,
button:focus {
    background: #0053ba;
}

button:focus {
    outline: 1px solid #fff;
    outline-offset: -4px;
}

button:active {
    transform: scale(0.99);
}
@media screen and  (max-width: 480px){
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }
    .c-title{
        font-size: 30px;
    }
    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    }
    .c-desc {
        display: none;
    }
    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }
    input{
        width: 35%;
        height: 40px;
        margin: 15px;
        margin-left: 0;
    }
    button{
        margin-top: 10px;
    }
    textarea{
        margin-top: 35px;
    }
}